body {
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #FFF;

}

.wrapper {
  @include block(600px, 905px);
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -300px 0 0 -455px;
  background-color: #DCDCDC;
  padding: 10px;
  overflow: hidden;
}

#container {
  @include block(580px, 600px);
  position: fixed;
  overflow: hidden;
  float: right;
  margin-left: 285px;
}

.panel {
  img:hover {
    border: 2px solid #1E90FF;
  }
  @include block(580px, 280px);
  padding: 5px;
  text-transform: uppercase;
  color: #5f5f5f;
  font: bold 12px arial;
  background: #F5F5F5;
  .panel-title {
    background: grey;
    color: #fff;
    padding: 7px;
    margin-bottom: 5px;
  }
  .panel-1 {
    height: 430px;
    margin-bottom: 10px;
    overflow: hidden;
    .passive_text {
      p {
        text-align: center;
      }
      .your_text {
        margin: 0 auto;
        display: block;
        width: 100%;
        resize: none;
        border: 1px solid #e3e3e3;
        padding: 5px;
        @include border-radius(3px);
        height: 45px;
      }
      .formats-set {
        margin: 10px 0;
        .active_font{
          font-size: 12px;
          line-height: 1;
          padding: 4px 7px;
          white-space: nowrap;
          text-transform: capitalize;
          color: #5f5f5f;
          margin-right: 10px;
          border: 1px solid #e3e3e3;
          cursor: pointer;
        }
        .active_fontSize{
          @extend .active_font;
        }
        .image_gallery{
          height: 300px;
          overflow-y: auto;
        }
      }
    }
  }
  .panel-2 {
    height: 130px;
    overflow: hidden;
  }
}

div.libery_head {
  height: 40px;
  margin-bottom: 0px;
  margin-top: 0px;
}

div.libery_head button {
  @include block(32px, 88px);
  line-height: 32px;
  font-weight: bold;
  text-transform: uppercase;
  text-align: center;
  color: #3a3a3a;
  background: $white;
  border: 1px solid $lightgrey;
  cursor: pointer;

}

.passive_navigator {
  text-transform: uppercase;
  color: #5f5f5f;
  font: normal 12px arial;
  font-weight: bold;
  box-sizing: border-box;
  overflow: hidden;
  ul {
    padding: 0;
    overflow-y: auto;
    height: 86px;
  }
}

.passive_navigator li {
  list-style: none;

}

.passive_navigator li:hover {
  cursor: pointer;
  background-color: #fff;
}

.selected {
  background: grey;
}

#close {
  position: relative;
  float: right;
  border: 1px solid #e3e3e3;
  background: #fff;
  cursor: pointer;
}

div .base_header {
  @include block(36px, 100%);
  line-height: 36px;
  border-bottom: 1px dashed #dadada;
  text-align: center;
  position: relative;
}

.passive_base img, .passive_prints img {
  @include block(61px, 61px);
  margin: 2px 1px;
}

.passive_base img, .passive_prints img {
  :nth-child(5) {
    margin-right: 0;
  }
}

.active {
  color: orange;
}

.filters {
  position: relative;

  .dropdown {
    position: relative;
    border: 1px solid $lightgrey;
    cursor: pointer;
    background: $white;
    @include border-radius(3px);
    @include user-select(none);
    margin: 10px 0;

    &.open {
      z-index: 2;
    }
    .carat {
      background: url("../images/arrows.png") no-repeat;
      @include block(20px, 20px);
      @include position(3px, 0, "", "");
    }
    .old {
      @include position(0, "", "", 0);
      @include block(0, 0);
      overflow: hidden;
    }
    select {
      @include position(0, "", "", 0);
    }
    .touch .old {
      @include block(100%, 100%);
    }
    .touch select {
      width: 100%;
      height: 100%;
      opacity: 0;
    }
    .dropdown .selected::after,
    .dropdown.scrollable div::after {
      @include pointer-events(none);
    }
    .selected,
    .dropdown li {
      display: block;
      font-size: 12px;
      line-height: 1;
      padding: 4px 7px;
      overflow: hidden;
      white-space: nowrap;
      text-transform: capitalize;
      color: $darkgrey;
    }
    .disabled .selected {
      color: #999;
    }
    .selected::after {
      @include position(0, 0, 0, "");
      width: 60px;
      border-radius: 0 2px 2px 0;
      box-shadow: inset -55px 0 25px -20px #fff;
    }
    ul {
      position: absolute;
      left: 0;
      top: 0;
      @include block(100%, 100%);
      list-style: none;
      padding: 0;
      text-transform: capitalize;
    }
    div {
      @include position(100%, -1px, "", -1px);
      height: 0;
      margin-top: -1px;
      background: #fff;
      border: 1px solid $lightgrey;
      border-top: none;
      border-radius: 0 0 3px 3px;
      overflow: hidden;
      opacity: 0;
      li {
        list-style: none;
        padding: 5px 12px;
        font-size: 12px;
        div {
          ::after {
            @include transition(all 150ms ease-in-out);
          }
        }
      }
    }
  }

  .selected {
    background: #fff !important;
  }

  /* Height is adjusted by JS on open */

  .dropdown.open div {
    opacity: 1;
    z-index: 2;
    overflow-y: auto;
  }

  /* FADE OVERLAY FOR SCROLLING LISTS */

  .dropdown.scrollable div::after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: 50px;

    box-shadow: inset 0 -50px 30px -35px #fff;
  }

  .dropdown.scrollable.bottom div::after {
    opacity: 0;
  }

  /* DROP DOWN LIST */

  .dropdown.scrollable.open ul {
    overflow-y: auto;
  }

  /* .focus class is also added on hover */

  .dropdown li.focus {
    position: relative;
    z-index: 3;
    color: $blue;
  }

  .dropdown li.active {
    font-weight: 700;
  }

  .dropdown.focus.open .carat {
    display: none;
  }
  .filter-2 {
    .dropdown {
      width: 85%;
      float: right;
      margin-top: 0px;
    }
  }
  .b-dashed {
    @include block(15px, 17px);
    margin-left: 18px;
    border-width: 0 0 1px 1px;
    border-color: #b8b8b8;
    border-style: dashed;
    float: left;
  }
  .filter-3 {
    display: flex;
    justify-content: space-between;
    border-top: 1px dashed #dadada;
    .left-side {
      width: 50%;
      float: left;
      margin-right: 1%;
    }
    .right-side {
      width: 50%;
      float: right;
      margin-left: 1%;
      form {
        position: relative;
        margin-top: 10px;
        input {
          padding-left: 20px;
          font-size: 12px;
          border: 1px solid $lightgrey;
          outline: none;
          @include block(22px, 100%);
          @include border-radius(3px);
        }
        .icon {
          position: absolute;
          top: 1px;
          left: 2px;
          background: url("../images/search.png") no-repeat center center #fff;
          border: 0 none;
          @include block (20px, 20px);
          padding: 0;
          filter: alpha(Opacity=15);
          opacity: .15;
          box-shadow: none;
        }
      }
    }
  }
}